  height: 23px;
}
.history ul {
  flex: 1;
  list-style: none;
  overflow: auto;
  padding-bottom: 40px;
}
.history li {
  transition: all 1s;
}
.history li:not(.special) {
  display: flex;
  margin: 10px 0;
  margin-left: 20px;
  position: relative;
  border: 1px solid transparent;
  cursor: pointer;
  
}
.history li:not(.special)::after {
  position: absolute;
  content: '';
  display: block;
  left: -20px;
  top: 50%;
  width: 10px;
  height: 10px;
  margin-top: -5px;
  background: #eee;
  border-radius: 50%;
  transition: background .5s;
}
.history li:not(.special).current::after {
  background: green;
}
.history li:not(.special):hover {
  border: 1px solid rgba(0, 200, 0, 0.5);
}
.history li.special {
  font-size: 20px;
  opacity: 0.1;
}
.history .key {
  flex: 1;
  
}
.history .key:nth-child(odd) {
  background: #efefef;
}
.history .result {
  text-align: center;
}
.history .tag {
  text-align: center;
  opacity: 0.6;
}
.history .empty {
  opacity: 0.6;
}
